import React, {Components} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core/styles';
import {DropzoneArea} from 'material-ui-dropzone';

const translations = window.props.translations

const FileUpload =({field, classes, addCourseDetails, value, type}) =>{
    const handleFieldChange=(file)=>{
        addCourseDetails({name:field.name, value:file})
    }
    return (
        <div >
            <div id="dropzone">
                <DropzoneArea 
                onChange={(e)=>handleFieldChange(e)}
                filesLimit={type==="image" ? 1 : 3}
                acceptedFiles={type==="image" ? ['image/*']:['image/*', 'video/*', 'application/*']}
                dropzoneText={translations.dragAndDropInfo}
                showAlerts={false}
                showPreviewsInDropzone={type==="image" ? false : true}
            />
            </div>
            <div>
                {(value && value[0] && type==="image") && <img style={{maxWidth:"300px",paddingTop:20}} src={URL.createObjectURL(value[0])} alt="course illustration"/>}
            </div>
        </div>
    )  
}

export default withStyles(styles)(FileUpload)